.Location {
	.route {
		width: 100%;
		height: 200px;
		background-color: #333;
		display: flex;
		justify-content: space-around;
		color: #efefef;
		padding-top: 30px;
		text-align: center;
		border-radius: 30px;
		li {
			width: 33.333%;
			border-right: 1px solid #fff;
			height: 80%;
			&:last-of-type {
				border: none;
			}
			.fa-bus,
			.fa-train-subway,
			.fa-car {
				font-size: 70px;
				margin-bottom: 20px;
			}
			.txt {
				h2 {
					font-size: 30px;
					font-family: 'roboto';
					margin-bottom: 10px;
				}
				p {
					font-family: 'roboto';
				}
			}
		}
	}
	#map {
		width: 100%;
		height: 500px;
		background-color: #000;
		margin-top: 100px;
		border: 1px solid #000;
	}
	button {
		margin-top: 10px;
		cursor: pointer;
		height: 30px;
		border: 1px solid #888;
		padding: 5px 20px;
	}
	.branch {
		width: 100%;
		margin-top: 50px;
		li {
			width: 100%;
			height: 300px;
			padding-right: 10px;
			margin-bottom: 50px;
			border: 1px solid #000;
			cursor: pointer;
			transition: 0.5s;
			&:hover {
				background: #999;
			}
			&.on {
				background: #999;
				border: none;
				color: #efefef;
			}
			.wrap {
				width: 100%;
				height: 100%;
				position: relative;

				img {
					width: 35%;
					height: 100%;
					object-fit: cover;
					float: left;
					position: absolute;
					top: 0;
					left: 0;
				}
				.txt {
					font-family: 'Gowun Dodum';
					position: absolute;
					top: 10%;
					left: 40%;
					h2 {
						color: #293d69;
						margin-bottom: 30px;
					}
				}
			}
			&:nth-of-type(2) {
				margin-right: 2%;
			}
			&:nth-of-type(2),
			&:nth-of-type(3) {
				float: left;
				width: 49%;
				img {
					width: 40%;
				}
				.txt {
					font-family: 'Gowun Dodum';
					position: absolute;
					top: 10%;
					left: 45%;
					h2 {
						color: #293d69;
						margin-bottom: 30px;
					}
				}
			}
		}
	}
}
@media screen and (max-width: $tablet) {
	.Location {
		.branch {
			width: 100%;
			margin-top: 50px;
			li {
				.wrap {
					img {
						width: 40%;
					}
					.txt {
						left: 45%;
						h2 {
							color: #293d69;
							margin-bottom: 30px;
						}
					}
				}
				&:nth-of-type(2),
				&:nth-of-type(3) {
					width: 100%;
					margin-right: 0;
				}
			}
		}
	}
}
